import React, { Suspense, lazy } from 'react';
import { Switch, Route, NavLink, useRouteMatch } from 'react-router-dom';

import DashboardPage from 'modules/home/pages';
import LoadingPage from 'components/LoadingPage';
import NotFoundPage from 'components/NotFoundPage';

import { ReactComponent as CalendarIcon } from 'assets/svg/calendar.svg';
import { ReactComponent as UserIcon } from 'assets/svg/user.svg';

const UsersList = lazy(() => import('../modules/users/view'));
const Schedule = lazy(() => import('../modules/schedules/view'));

const Dashboard: React.FC = () => {
  const { path } = useRouteMatch();

  return (
    <DashboardPage
      links={[
        <NavLink exact to={`${path}`}>
          <CalendarIcon />
          Agendamentos
        </NavLink>,

        <NavLink to={`${path}/usuarios`}>
          <UserIcon />
          Usuários
        </NavLink>,
      ]}
    >
      <Suspense fallback={<LoadingPage />}>
        <Switch>
          <Route exact path={path} component={Schedule} />
          <Route path={`${path}/usuarios`} component={UsersList} />

          <Route path="*" component={NotFoundPage} />
        </Switch>
      </Suspense>
    </DashboardPage>
  );
};

export default Dashboard;
