import { css } from 'styled-components';

export const flexCenter = css`
  display: flex;
  align-items: center;
`;

export const centerMiddle = css`
  display: flex;
  align-items: center;
  justify-content: center;
`;

export const centerMiddleInline = css`
  display: inline-flex;
  align-items: center;
  justify-content: center;
`;

export const centerMiddleColumns = css`
  ${centerMiddle}
  flex-direction: column;
`;
