import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';

import { ReactComponent as MenuIcon } from 'assets/svg/menu_icon.svg';
import { ReactComponent as LogoutIcon } from 'assets/svg/logout_icon.svg';
import LogoImage from 'assets/img/rioarte.png';
import { signOut } from '../../login/states/auth/actions';

import {
  Container,
  Logo,
  LogoContainer,
  LogoutButton,
  Menu,
  MenuButton,
  MenuContainer,
  Navbar,
  TopBar,
  Page,
  SideBar,
  PageContent,
} from './styles';

import { DashboardProps } from '../model/props';

const Dashboard: React.FC<DashboardProps> = ({ children, links }) => {
  const history = useHistory();
  const dispatch = useDispatch();
  const [menuOpen, setMenuOpen] = useState(true);

  const handleClickLogoutButton = () => {
    localStorage.clear();
    dispatch(signOut());
    history.push('/');
  };

  const handleClickMenuButton = () => {
    setMenuOpen(!menuOpen);
  };

  return (
    <Container>
      <TopBar>
        <LogoContainer menuOpen={menuOpen}>
          <Logo src={LogoImage} alt="RIOARTE" />
        </LogoContainer>

        <MenuContainer>
          <Menu>
            <MenuButton onClick={handleClickMenuButton} aria-label="Botão menu">
              <MenuIcon />
            </MenuButton>

            <Navbar>{links}</Navbar>

            <LogoutButton onClick={handleClickLogoutButton}>
              <LogoutIcon />
              Sair
            </LogoutButton>
          </Menu>
        </MenuContainer>
      </TopBar>

      <Page>
        <SideBar id="sideBar" menuOpen={menuOpen} />

        <PageContent>{children}</PageContent>
      </Page>
    </Container>
  );
};

export default Dashboard;
