import styled, { css } from 'styled-components';
import colors from 'utils/styles/colors';
import { centerMiddle, flexCenter } from 'utils/styles/commonStyles';

interface LayoutProps {
  menuOpen: boolean;
}

export const Container = styled.div`
  display: flex;
  flex-direction: column;
  height: 100vh;
`;

export const TopBar = styled.div`
  display: flex;
  height: 54px;

  @media (max-width: 900px) {
    flex-direction: column;
    height: auto;
  }
`;

export const LogoContainer = styled.figure<LayoutProps>`
  ${centerMiddle}
  width: 293px;
  height: 100px;
  background: ${colors.secondary};
  overflow: hidden;
  transition: all 0.25s;
  z-index: 999;

  ${({ menuOpen }) =>
    !menuOpen &&
    css`
      width: 0;
    `}

  @media (max-width: 900px) {
    width: 100%;
    height: 64px;
    transition: none;
  }
`;

export const Logo = styled.img`
  width: 114px;
`;

export const MenuContainer = styled.div`
  position: relative;
  flex: 1;
`;

export const Menu = styled.div`
  ${flexCenter}
  position: absolute;
  width: 100%;
  height: 54px;
  padding: 0 30px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.07);
  z-index: 999;

  @media (max-width: 430px) {
    padding: 0 1%;
  }

  @media (max-width: 355px) {
    overflow: auto;
  }
`;

export const MenuButton = styled.button`
  background: none;
  border: 0;
  height: 100%;
  width: 40px;
  margin-right: 16px;
  outline-color: ${colors.primary};
`;

export const LogoutButton = styled.button`
  ${centerMiddle}
  font-size: 13px;
  background: none;
  border: 0;
  padding: 8px;
  outline-color: ${colors.primary};
  color: ${colors.black_alt};

  svg {
    width: 24px;
    margin-right: 10px;
  }
`;

export const Navbar = styled.nav`
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  flex: 1;

  a {
    ${centerMiddle}
    height: 100%;
    color: ${colors.black_alt};
    text-transform: uppercase;
    text-decoration: none;
    font-size: 13px;
    outline-color: ${colors.primary};

    &.active {
      font-weight: 600;
      background-color: ${colors.white};
    }

    & + a {
      margin: 0 16px;
    }
    svg {
      margin: -1px 10px 0 0;
    }

    @media (max-width: 430px) {
      & + a {
        margin: 0 8px;
      }
    }
  }
`;

export const Page = styled.div`
  display: flex;
  flex: 1 1 auto;
  height: calc(100% - 118px);

  @media (max-width: 900px) {
    margin-top: 54px;
  }
`;

export const SideBar = styled.div<LayoutProps>`
  position: relative;
  width: ${props => (props.menuOpen ? '293px' : '0px')};
  background-color: ${colors.secondary};
  padding-top: 48px;
  transition: all 0.25s;
  overflow: auto;
  height: 100%;

  @media (max-width: 900px) {
    width: ${props => (!props.menuOpen ? '50%' : '0px')};
  }

  @media (max-width: 600px) {
    position: absolute;
    width: ${props => (!props.menuOpen ? '100%' : '0px')};
    z-index: 999;
  }
`;

export const PageContent = styled.div`
  flex: 1;
  height: 100%;
  overflow: auto;
`;
